    export default {
        data() {
            return {
                showtime: false,   // 显示自定义事件弹窗
                startTime: null,  //  开始时间
                entTime: null,  //  结束时间
                start: null,
                end: null,
                show: false,  // 判断筛选弹窗显示或隐藏
                shopName: ["设备故障","菜品不好","态度不好","环境不好","其他"],
                company_name: "",  // 类型
                page: 1,            //  呼叫列表请求页码页数
                maxPage: 1,
                onFetching: false,
                loadText: '正在拼命加载中',        //  呼叫中列表  下拉提示词
                loading: true,                  //   呼叫中列表   下拉圈圈
                limit: 10,     //  一次限制请求几条数据
                value: 0,   //  默认显示昨天
//                投诉列表
                overList: [],
                type_id: null,    //  投诉类型ID
            }
        },
        created(){
            this.startTime = this.common.commonFun.get_date(1);
            this.entTime = this.common.commonFun.get_date(1);
            this.getInfo();
        },
        methods:{
            getInfo(){
                if (this.onFetching) {
                    // do nothing
                } else {
                    this.onFetching = true;
                    if(this.page > this.maxPage){
                        this.onFetching = false;
                        return;
                    }
                    this.common.commonFun.ajax({
                        dom: this,
                        url: this.common.apiUrl.getServiceAbnormalStatistics,
                        type: "post",
                        data: {
                            page: this.page,
                            limit: this.limit,
                            time: this.startTime,
                            times: this.entTime,
                            type_id: this.type_id
                        },
                        success: (response, allResponse) => {
                            // console.log('response',response)
                            this.onFetching = true;
                            this.maxPage = Math.ceil(allResponse.count/this.limit) || 1;
                            if(this.page >= this.maxPage){
                                this.onFetching = false;
                                console.log('滚动结束');
                                this.loadText = "已经到底了哦~";
                                this.loading = false;
                            }
                            this.overList = this.overList.concat(response);
                            this.onFetching = false;
                            this.page ++;
                        }
                    });
                }
            },
            //            自定义弹窗=>点击确定
            onConfirm(){
                this.start = this.startTime.replace(/\-/g,'.').substring(5,10);
                this.end = this.entTime.replace(/\-/g,'.').substring(5,10);
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo();
            },
            //            点击筛选触发
            navRigth(){
                this.show = true;
            },
            //            筛选弹窗确定按钮
            choice(){
                this.show = false;
                this.shopName.forEach((item,index)=>{
                    if(this.company_name == item){
                        this.type_id = index + 1
                    }
                });
                this.page = 1;
                this.maxPage = 1;
                this.overList = [];
                this.$refs.scrollerBottom.reset({top: 0});
                this.getInfo()
            },
            //            选择时间
            date(index){
                this.value = index;
                this.page = 1;
                this.maxPage = 1;
                if(index == 0){
                    this.overList = [];
                    this.startTime = this.common.commonFun.get_date(1);
                    this.entTime = this.common.commonFun.get_date(1);
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.getInfo();
                }
                if(index == 1){
                    this.overList = [];
                    this.startTime = this.common.commonFun.get_date(0);
                    this.entTime = this.common.commonFun.get_date(0);
                    this.$refs.scrollerBottom.reset({top: 0});
                    this.getInfo();
                }
                if(index == 2){
                    this.showtime = true;
                }
            },
            //            异常列表=>已解决界面=>查原由按钮
            origin(item){
                this.$router.push({
                    name: "lookReason",
                    params:{
                        complaint: 'complaint',
                    },
                    query:{
                        id: item.service_cont_id
                    }
                })
            }
        }
    }



